.H1 {
  color: black;
}

.Buttons {
  display: flex;
  justify-content: space-between;
}

.Cont {
  display: inline-flex;
  text-align: center;
}

.Trans {
  animation: trans 0.3s ease-out forwards;
}

@keyframes trans {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
