.NavigationItems {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-flow: column;
  height: 100%;
}

.Cart {
  width: 50px;
}

@media (min-width: 500px) {
  .NavigationItems {
    flex-flow: row;
    align-items: center;
  }
  .Cart {
    width: 50px;
  }
}
